// Document Prep
* {
  margin: 0;
  padding: 0;
}

body {
  background: #fff;
  color: #000;
  @media(prefers-color-scheme: dark) {
    background: #000;
    color: #fff;
  }
  font-family: 'Inter', sans-serif;
  margin: 0 auto;
  max-width: 100%;
  scroll-behavior: smooth;
}

// Document Highlighting
::selection { background: #fdfd96; }

// Typography
@media only screen and (max-device-width: 480px) {
  h1 { font-size: 28px !important; }
  h2 { font-size: 21px !important; }
  h3 { font-size: 19px !important; }
  h4, h5, h6 { font-size: 16px !important; }
}

// Navigation
header {
  text-align: center;
  display: flex;
  justify-content: space-between;
  height: 100px;
  background: #fdfd96;
  padding-left: 16px;
  * {
    cursor: default;
    font-size: 14px;
  }
}

nav {
  ul {
    flex: 0 0 auto;
    text-align: center;
    display: flex;
    margin-top: 2.5em;
    li {
      list-style: none;
      margin: 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      :first-child { margin-left: 0; }
      a {
        cursor: pointer;
        padding: 0 0.8em;
        font-size: 1.1em;
        text-decoration: none;
        color: #000;
      }
      a:hover {
        text-decoration: underline;
        transition: all 150ms ease-in-out;
      }
      :last-child { a { padding-right: 0; } }
    }
  }
}

// Header
.header {
  justify-content: center;
  background: #fdfd96;
  color: #000;
  padding: 1.5em 32px;
  max-height: 30vh;

  p, b {
    flex: 0 0 auto;
    margin: auto 16px;
    display: flex;
    justify-content: center;

    font-size: 80px;
    letter-spacing: -.05em;
    font-weight: 500;
    line-height: 1.1;
    white-space: nowrap;
  }
  b { font-weight: 700 !important; }
}

// Section
.section {
  margin: 1em 10em;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  .text, .image { 
    padding: 1em 1em;
  }
  .image img{
    width: 250px;
    height: 250px;
  }
  @media(max-device-width: 480px) { 
    margin: 1em 2.5em;
    flex-direction: column;
    .text, .image {
      width: 100%;
    }
  }
}

// Container
.container {
  margin: 1em 10em;
  h1, h2, h3, h4, h5, h6 {
    margin-top: .75em;
    margin-bottom: .20em;
  }
}

// Footer
footer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  text-align: center;
  padding-bottom: .5em;
  p {
    font-size: .8em;
    a {
      color: #000 !important;
      @media(prefers-color-scheme: dark) {
        color: #fff !important;
      }
      text-decoration: underline;
    }
  }
}